.App {
    height: 100vh;
}

.Summary {
    overflow-y: scroll;
    position: relative;
}

.Controls {
    display: grid;
    grid-template-columns: repeat(4, 1fr);
    grid-gap: 0px 0px;
}

table {
    width: 100%
}
thead {
    font-weight: bold;
}
tr:nth-child(even) {
    background: lightgreen;
}

.quantity {
    text-align: right;
}
.price {
    font-variant-numeric: tabular-nums;
    text-align: right;
}

.soft {
    background-color: green;
    color: white;
}
.medium {
    background-color: yellow;
}
.hard {
    background-color: red;
}

@media (orientation: landscape) {
    .App {
        display: grid;
        grid-template-columns: repeat(2, 1fr);
        grid-gap: 0px 0px;
    }
}

@media (orientation: portrait) {
    .Summary {
        height: 50vh;
    }
    .Controls {
        height: 50vh;
    }
}
